
import { defineComponent, reactive } from 'vue';

export default defineComponent({
  name: "CHeader",
  setup(){
    const data = reactive({
      menuItems: [
        {path: '#', title: ''},
        {path: '#', title: ''},
        {path: '#', title: ''},
        {path: '#', title: ''},
      ]
    });
    function menuSwitch(){
      let collapseMenu: Element|null = document.querySelector('.nav-collapse-menu');
      collapseMenu?.classList.toggle('isOpenMenu');
    }
    return {
      data,
      menuSwitch
    }
  }
});
